// react
import React from 'react'

// redux
import { Provider as Redux } from 'react-redux'
import store from './store'

// @react-router
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'

// @styled-components
import { ThemeProvider } from 'styled-components'

// providers
import { GraphQLProvider, data } from './graph'
import theme from './theme'

// antd
import { Layout, Menu } from 'antd'

// components
import Home from './pages/home'

// stylesheet
import './global.css'
import 'antd/dist/antd.css'; // or 'antd/dist/antd.less'

// destructuring
const { Header , Content} = Layout

/**
 * Exports
 */
const App = () => (
  <Redux store={store}>
    <GraphQLProvider>
      <ThemeProvider theme={theme}>
        <Router>
          <Layout>
            <Header style={{ position: `fixed`, zIndex: 101, width: `100%` }}>
              <Menu
                theme="dark"
                mode="horizontal"
                defaultSelectedKeys={['2']}
                style={{ lineHeight: '64px' }}>
                <Menu.Item key="0" style={{color: `white`, textTransform: `uppercase`, letterSpacing: `0.2ch`, fontWeight: 700}}>
                  <Link to={`/`}>Tax The Rich</Link>
                </Menu.Item>
                <Menu.Item key="1">
                  <Link to={`/`}>Take Action</Link>
                </Menu.Item>
              </Menu>
            </Header>
            <Content style={{marginTop: `64px`}}>
              <Home actions={data.actions} />
            </Content>
          </Layout>
        </Router>
      </ThemeProvider>
    </GraphQLProvider>
  </Redux>
)

export default App
